import React, { useEffect, useRef, useState } from "react";

import beepSound from "../audio/bell.wav";

const Notifier = ({ shouldPlay, setShouldPlay }) => {
	// const [soundPlaying, setSoundPlaying] = useState(false);
	const audioRef = useRef(null);

	useEffect(() => {
		if (shouldPlay && audioRef.current) {
			console.log("playing audio");
			audioRef.current.loop = true;
			audioRef.current
				.play()
				.catch((e) => console.warn("Auto-play failed:", e));
		}
	}, [shouldPlay]);

	const stopSound = () => {
		if (audioRef.current) {
			audioRef.current.pause();
			audioRef.current.currentTime = 0;
		}

		setShouldPlay(false);
	};

	return (
		<div>
			<audio ref={audioRef} src={beepSound} loop preload="auto" />

			<button
				className={`btn ${shouldPlay ? "btn-primary" : "btn-dark"}`}
				onClick={shouldPlay ? stopSound : undefined}
				disabled={!shouldPlay}
			>
				{shouldPlay ? "Stop Sound" : "No Alerts"}
			</button>
		</div>
	);
};

export default Notifier;
