import { useEffect, useRef, useState } from "react";

import CardsContainer from "./components/CardsContainer";
import Controls from "./components/Controls";
import ConnectionStatus from "./components/ConnectionStatus";
import LatestAlertBanner from "./components/LatestAlertBanner";
import MainContainer from "./components/MainContainer";
import Notifier from "./components/Notifier";
import MenuToggle from "./components/MenuToggle";
import useFirebaseMessages from "./hooks/useFirebaseMessages";
import { useGlobalState } from "./context/GlobalStateContext";

function App() {
	const { isMuted, perCardMute } = useGlobalState();
	const [shouldPlay, setShouldPlay] = useState(false);
	const { messages, isInitialLoad } = useFirebaseMessages();

	const lastMessageRef = useRef(null);

	useEffect(() => {
		if (isInitialLoad || messages.length === 0) return;

		const latest = messages[messages.length - 1];
		const ticker = latest.ticker || "KAPS";

		// First load
		if (!lastMessageRef.current) {
			// Set without triggering sound
			lastMessageRef.current = latest;

			// if (!(ticker in perCardMute)) {
			// 	dispatch({
			// 		type: "SET_PER_CARD_MUTE",
			// 		payload: { ticker, value: false },
			// 	});
			// }

			return;
		}

		// If new alert
		if (lastMessageRef.current.timestamp !== latest.timestamp) {
			// if (!(ticker in perCardMute)) {
			// 	dispatch({
			// 		type: "SET_PER_CARD_MUTE",
			// 		payload: { ticker, value: false },
			// 	});
			// }

			const isCardMuted = perCardMute[ticker] ?? false;

			console.log("New message:", latest);
			console.log("Ticker:", ticker);
			console.log("Global muted:", isMuted);
			console.log("Card muted:", isCardMuted);

			if (!isMuted && !isCardMuted) {
				setShouldPlay(true);
			}

			lastMessageRef.current = latest;
		}
	}, [messages, isInitialLoad, isMuted, perCardMute]);

	return (
		<div
			className="position-relative min-vm-100 min-vh-100 p-0 m-0"
			style={{ backgroundColor: "#222" }}
		>
			<LatestAlertBanner />
			<MenuToggle setShouldPlay={setShouldPlay} />

			<MainContainer>
				<ConnectionStatus />
				<Notifier
					shouldPlay={shouldPlay}
					setShouldPlay={setShouldPlay}
				/>
				<Controls />
			</MainContainer>

			<CardsContainer messages={messages} />
		</div>
	);
}

export default App;
