import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars } from "@fortawesome/free-solid-svg-icons";
import { useGlobalState } from "../context/GlobalStateContext";
import { db } from "../db/firebase";

const MenuToggle = ({ setShouldPlay }) => {
	const state = useGlobalState();
	const { allMessages } = useGlobalState();

	const displayState = {
		...state,
		allMessages: `${state.allMessages.length}`,
	};

	const handleNewDay = async () => {
		const timestamp = Date.now();
		const formatted = new Date(timestamp).toLocaleString("en-gb", {
			hour: "2-digit",
			minute: "2-digit",
			day: "2-digit",
			month: "2-digit",
			year: "numeric",
		});

		await db.ref("messages").push({
			content: "NEW DAY",
			timestamp,
			formattedTimestamp: formatted,
		});

		await db.ref("status").set({
			event: "NEW_DAY",
			timestamp,
		});

		console.log("New day added");
	};

	const handleRefresh = () => {
		const timestamp = Date.now();
		db.ref("commands/refresh").set({ timestamp });
		console.log("Refresh triggered");
	};

	const handleTest = () => {
		setShouldPlay(true);
		console.log("Test alert triggered");
	};

	return (
		<>
			<div className="position-absolute left-0 m-3">
				<button
					style={{ background: "none", border: "none" }}
					type="button"
					data-bs-toggle="offcanvas"
					data-bs-target="#offcanvasNavbar"
					aria-controls="offcanvasNavbar"
				>
					<FontAwesomeIcon
						icon={faBars}
						style={{
							color: "#dce1ea",
							width: "25px",
							height: "25px",
						}}
					/>
				</button>
			</div>

			{/* Off canvas */}
			<div
				className="offcanvas offcanvas-end"
				tabIndex="-1"
				id="offcanvasNavbar"
				aria-labelledby="offcanvasNavbarDarkLabel"
				style={{
					backgroundColor: "#222",
					color: "#fff",
					width: "300px",
					boxShadow: "0 0 20px rgba(255, 255, 255, 0.1)",
				}}
			>
				<div className="offcanvas-header">
					<h5 id="offcanvasNavbarDarkLabel" className="text-white">
						Menu
					</h5>
					<button
						type="button"
						className="btn-close btn-close-white"
						data-bs-dismiss="offcanvas"
						aria-label="Close"
					></button>
				</div>

				<div className="offcanvas-body">
					<ul className="nav flex-column gap-3">
						<li>
							<button
								onClick={handleNewDay}
								className="btn btn-primary w-100"
							>
								New Day
							</button>
						</li>
						<li>
							<button
								onClick={handleRefresh}
								className="btn btn-primary w-100"
							>
								Refresh
							</button>
						</li>
						<li>
							<button
								onClick={handleTest}
								className="btn btn-primary w-100"
							>
								Test
							</button>
						</li>

						<hr className="text-secondary" />

						<h5
							id="offcanvasNavbarDarkLabel"
							className="text-white"
						>
							Data
						</h5>
						<li>
							<pre
								style={{
									backgroundColor: "#222",
									color: "#ddd",
									padding: "10px",
									borderRadius: "6px",
									maxHeight: "300px",
									overflowY: "auto",
									fontSize: "12px",
								}}
							>
								{JSON.stringify(displayState, null, 2)}
							</pre>
						</li>

						<hr className="text-secondary" />

						<h5
							id="offcanvasNavbarDarkLabel"
							className="text-white"
						>
							Raw Messages
						</h5>
						<div
							style={{
								maxHeight: "200px",
								overflowY: "auto",
								fontSize: "0.8rem",
								color: "#aaa",
								whiteSpace: "pre-wrap",
							}}
						>
							{allMessages.map((msg, index) => (
								<div key={index}>
									<code>{JSON.stringify(msg, null, 2)}</code>
									<hr style={{ borderColor: "#444" }} />
								</div>
							))}
						</div>
					</ul>
				</div>
			</div>
		</>
	);
};

export default MenuToggle;
