import React, { createContext, useReducer, useContext } from "react";

const initialState = {
	isMuted: false,
	connectionStatus: "disconnected",
	alert: null,
	allMessages: [],
	tickers: {},
	selectedTickers: new Set([
		"NQ",
		"ES",
		"RTY",
		"YM",
		"ZS",
		"CL",
		"GC",
		"ZW",
		"BTC",
		"BP",
		"NG",
		"LE",
		"PL",
		"ZC",
		"SI",
		"6E",
	]),
	isInitialLoad: true,
	refreshTimestamp: null,
	tickerNames: {
		NQ: "Nasdaq",
		ES: "S&P 500",
		RTY: "Russell 2000",
		YM: "Dow Jones",
		ZS: "Soybeans",
		CL: "Crude Oil",
		GC: "Gold",
		ZW: "Wheat",
		BTC: "Bitcoin",
		BP: "British Pound",
		NG: "Natural Gas",
		LE: "Live Cattle",
		PL: "Platinum",
		ZC: "Corn",
		SI: "Silver",
		"6E": "Euro FX",
	},
	isMessagePanelVisible: false,
	soundPlaying: false,
	perCardMute: {
		NQ: false,
		ES: false,
		RTY: false,
		YM: false,
		ZS: false,
		CL: false,
		GC: false,
		ZW: false,
		BTC: false,
		BP: false,
		NG: false,
		LE: false,
		PL: false,
		ZC: false,
		SI: false,
		"6E": false,
		KAPS: false, // don’t forget KAPS!
	},
};

// Reducer function
const reducer = (state, action) => {
	switch (action.type) {
		case "TOGGLE_MUTE":
			return { ...state, isMuted: !state.isMuted };
		case "SET_CONNECTION_STATUS":
			return { ...state, connectionStatus: action.payload };
		case "SET_ALERT":
			return { ...state, alert: action.payload };
		case "ADD_MESSAGE":
			return {
				...state,
				allMessages: [...state.allMessages, action.payload],
			};
		case "SET_REFRESH_TIMESTAMP":
			return { ...state, refreshTimestamp: action.payload };
		case "TOGGLE_MUTE":
			const newMute = !state.isMuted;
			const resetMutes = {};
			Object.keys(state.perCardMute).forEach((ticker) => {
				resetMutes[ticker] = newMute;
			});
			return {
				...state,
				isMuted: newMute,
				perCardMute: resetMutes,
			};
		case "SET_PER_CARD_MUTE":
			return {
				...state,
				perCardMute: {
					...state.perCardMute,
					[action.payload.ticker]: action.payload.value,
				},
			};
		case "RESET_ALL_CARD_MUTES":
			const resetMuted = {};
			Object.keys(state.perCardMute).forEach(
				(ticker) => (resetMuted[ticker] = action.payload)
			);
			return {
				...state,
				perCardMute: resetMuted,
			};
		default:
			return state;
	}
};

// Create context
const GlobalStateContext = createContext();
const DispatchContext = createContext();

// Provider
export const GlobalStateProvider = ({ children }) => {
	const [state, dispatch] = useReducer(reducer, initialState);

	return (
		<GlobalStateContext.Provider value={state}>
			<DispatchContext.Provider value={dispatch}>
				{children}
			</DispatchContext.Provider>
		</GlobalStateContext.Provider>
	);
};

// Custom hooks for ease of use
export const useGlobalState = () => useContext(GlobalStateContext);
export const useDispatch = () => useContext(DispatchContext);
