import React from "react";

const MainContainer = ({ children }) => {
	return (
		<div
			className="rounded container d-flex justify-content-between p-3 align-items-center"
			style={{
				// borderWidth: "2px",
				// borderColor: "#fff",
				// border: "2px #000 solid",
				backgroundColor: "#333",
			}}
		>
			{children}
		</div>
	);
};

export default MainContainer;
