// src/hooks/useFirebaseMessages.js
import { useEffect, useRef, useState } from "react";
import { db } from "../db/firebase";
import { useDispatch } from "../context/GlobalStateContext";

const useFirebaseMessages = () => {
	const [messages, setMessages] = useState([]);
	const [lastNewDayKey, setLastNewDayKey] = useState(null);
	const [isInitialLoad, setIsInitialLoad] = useState(true);
	const startListeningFrom = useRef(Date.now());
	const dispatch = useDispatch();

	useEffect(() => {
		// Initial load: fetch all messages
		db.ref("messages")
			.once("value")
			.then((snapshot) => {
				const data = snapshot.val();
				console.log("current data:", data);
				if (!data) return;

				const entries = Object.entries(data);
				const newMessages = [];
				let foundNewDay = false;
				let lastNewDay = null;

				for (const [key, message] of entries) {
					if (message.content === "NEW DAY") {
						console.log("found new day");
						lastNewDay = key;
						foundNewDay = true;
						newMessages.length = 0;
						continue;
					}

					newMessages.push({ key, ...message });
				}

				newMessages.forEach((msg) =>
					dispatch({ type: "ADD_MESSAGE", payload: msg })
				);

				setMessages(newMessages);
				if (lastNewDay) setLastNewDayKey(lastNewDay);
				setIsInitialLoad(false);

				console.log("new messages", newMessages);
			});
	}, []);

	useEffect(() => {
		if (!isInitialLoad) {
			const ref = db.ref("messages");

			const listener = ref.on("child_added", (snapshot) => {
				const message = snapshot.val();
				const key = snapshot.key;

				// Ignore historical messages;
				if (message.timestamp < startListeningFrom.current) return;

				// Clear if NEW DAY comes live
				if (message.content === "NEW DAY") {
					setMessages([]);
					setLastNewDayKey(key);
					return;
				}

				// Skip if message is before NEW DAY
				if (lastNewDayKey && key <= lastNewDayKey) return;

				const fullMessage = { key, ...message };

				// Add new message to array
				setMessages((prev) => [...prev, { key, ...message }]);

				dispatch({ type: "ADD_MESSAGE", payload: fullMessage });
			});

			return () => ref.off("child_added", listener);
		}
	}, [isInitialLoad, lastNewDayKey]);

	return { messages };
};

export default useFirebaseMessages;
