import React, { useState } from "react";
import { faVolumeMute, faVolumeUp } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { useDispatch, useGlobalState } from "../context/GlobalStateContext";
import "./card.css";

const Card = ({
	title = "",
	alerts = [],
	type = "STRING",
	ticker,
	latestMessageKey,
}) => {
	const dispatch = useDispatch();
	const { isMuted, perCardMute } = useGlobalState();

	const isCardMuted = perCardMute[ticker] ?? false;
	console.log("ticker:", ticker, "muted:", isCardMuted);

	const toggleCardMute = () => {
		dispatch({
			type: "SET_PER_CARD_MUTE",
			payload: { ticker, value: !isCardMuted },
		});
	};
	const isKAPS = type === "KAPS";
	const backgroundColor = isKAPS ? "#111" : "#333";
	const alertBackground = isKAPS ? "#222" : "#444";

	return (
		<div className="card shadow text-white" style={{ backgroundColor }}>
			<div className="card-header d-flex justify-content-between align-items-center">
				<div />
				<h5 className="mb-0">{title}</h5>
				<button
					className="btn btn-sm btn-outline-secondary"
					onClick={toggleCardMute}
					disabled={isMuted}
				>
					<FontAwesomeIcon
						icon={isCardMuted ? faVolumeMute : faVolumeUp}
						style={{ color: isCardMuted ? "red" : "inherit" }}
					/>
				</button>
			</div>
			<ul className="list-group list-group-flush">
				{alerts.length > 0 ? (
					alerts.map((alert, index) => {
						const isLatest = alert.key === latestMessageKey;
						const liClass = `list-group-item text-center ${
							isLatest
								? "bg-primary text-white fw-bold latest-alert-animate"
								: "text-white"
						}`;

						const timestamp = new Date(
							alert.timestamp
						).toLocaleDateString([], {
							hour: "2-digit",
							minute: "2-digit",
						});

						return (
							<li
								key={alert.key || index}
								className={liClass}
								style={{
									backgroundColor: isLatest
										? undefined
										: alertBackground,
								}}
							>
								{isKAPS ? (
									<>
										<span>{alert.content}</span>{" "}
										<span>{timestamp}</span>
									</>
								) : (
									<>
										<span>{alert.action}</span>{" "}
										<span>@ {alert.price}</span>
										<span>🕒 {timestamp}</span>
									</>
								)}
							</li>
						);
					})
				) : (
					<li className="list-group-item text-center text-muted">
						No alerts yet
					</li>
				)}
				{/* {alerts.length > 0 ? (
					alerts.map((alert, index) => (
						<li
							key={index}
							className="list-group-item text-center"
							style={{ backgroundColor: alertBackground }}
						>
							{isKAPS ? (
								<>
									<span className="text-white">
										{alert.content}
									</span>
									<span className="text-muted ms-2">
										🕒 {alert.time}
									</span>
								</>
							) : (
								<>
									<span className="fw-bold text-primary">
										{alert.action}
									</span>{" "}
									<span className="fw-bold text-white">
										@ {alert.price}
									</span>
									<span className="text-muted ms-2">
										🕒 {alert.time}
									</span>
								</>
							)}
						</li>
					))
				) : (
					<li className="list-group-item text-center text-muted">
						No alerts yet
					</li>
				)} */}
			</ul>
		</div>
	);
};

export default Card;
